@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Quicksand', sans-serif}html,body,#root,.app-wrap{height:100%}.app-wrap{display:grid;grid-template-rows:auto 1fr}nav{display:flex;justify-content:space-between;align-items:center;padding:1em;background:black;color:white}nav a{text-decoration:none;color:white;font-weight:500}nav a:hover{color:#ffaf8a}nav .right-nav{display:flex;align-items:center;list-style:none}nav .right-nav .basket-cont{margin-left:8px}nav .right-nav .basket-cont a{display:flex;align-items:center}nav .right-nav .basket-cont a svg{display:block;height:30px;stroke-width:1px;margin-right:-5px}

main{background-position:40% 20%;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:auto}main .right{padding:1em;display:flex;align-items:center;justify-content:center}main .right .box{box-sizing:content-box;display:flex;align-items:center;justify-content:space-around;flex-direction:column;background:rgba(255,255,255,0.87);padding:2em 4em;width:70%}main .right .box p{font-size:36px;text-align:center;margin-bottom:0.5em}main .right .box img{display:block;width:70%;height:auto;border-radius:50%}main .right .box a{text-decoration:none;color:white;font-size:28px;background:black;padding:8px 16px;transition:0.1s all ease-in-out;margin-top:0.5em}main .right .box a:hover{background:#2e2e2e;color:#ff8f5b}

section{background-position:40% 20%;background-repeat:no-repeat;background-size:cover;padding:3em}section .shop-box{padding:1em;background:white}section .shop-box h1{font-weight:400;font-size:36px;margin-bottom:16px}section .shop-box .products{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-gap:16px}section .shop-box .products>*{text-decoration:none;color:black}section .shop-box .products .item-card{cursor:pointer;transition:all 0.2s ease}section .shop-box .products .item-card:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}section .shop-box .products .item-card:hover .product-detais{background-color:#f1d4c6}section .shop-box .products .item-card img{display:block;width:100%}section .shop-box .products .item-card .product-detais{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:0.5em;transition:all 0.2s ease-in-out;text-decoration:none}section .shop-box .products .item-card .product-detais .product-title{font-weight:500}

.item-page{background-position:70% 50%;background-repeat:no-repeat;background-size:cover;padding:3em}.item-page .shop-box{display:flex;padding:1em;background:white}.item-page .shop-box .image{flex:1 1}.item-page .shop-box .image img{display:block;width:100%;height:auto}.item-page .shop-box .details{flex:1 1;padding:1em}.item-page .shop-box .details h1{font-weight:500;font-size:72px;margin-bottom:0}.item-page .shop-box .details .item-detail{font-weight:200;font-size:21px;margin-bottom:8px}.item-page .shop-box .details p{font-weight:200;font-size:36px;margin-bottom:8px}.item-page .shop-box .details button{cursor:pointer;padding:1em;border:none;background:#ffaf8a;transition:all 0.15s ease-in-out}.item-page .shop-box .details button:hover{-webkit-filter:brightness(0.9);filter:brightness(0.9)}.item-page .shop-box .details .quantity{display:flex;margin-bottom:8px}.item-page .shop-box .details .quantity .quant-btn{display:flex;align-items:center;justify-content:center;padding:0.5em}.item-page .shop-box .details .quantity .quant-btn svg{stroke-width:0.7px;height:35px}.item-page .shop-box .details .quantity .quant-amount{display:flex;justify-content:center;align-items:center;background:#ececec;padding:1em 1.5em}.item-page .shop-box .details a{text-decoration:none}.item-page .shop-box .details .outline{display:flex;align-items:center;padding:0.5em 0.25em;background:none;border:1px solid #ffaf8a}.item-page .shop-box .details .outline:hover{background:#ffaf8a}.item-page .shop-box .details .outline svg{stroke-width:0.75px;height:30px}

.basket-page{background-position:40% 20%;background-repeat:no-repeat;background-size:cover;padding:3em}.basket-page .shop-box{width:60%;margin:auto;padding:1em;background:white}.basket-page .shop-box .top{margin-bottom:0.5em;display:flex;justify-content:space-between;align-items:center}.basket-page .shop-box .top h2{font-weight:300;font-size:36px;margin-bottom:0.25em}.basket-page .shop-box .top button{cursor:pointer;padding:0.5em;font-size:28px;border:none;background:#ffaf8a;transition:all 0.15s ease-in-out}.basket-page .shop-box .top button:hover{-webkit-filter:brightness(0.9);filter:brightness(0.9)}.basket-page .shop-box .order-total{text-align:right;font-size:21px}

.basket-item{display:flex;margin-bottom:1em}.basket-item img{height:auto;max-width:200px}.basket-item .item-details{padding:1em;flex:1 1}.basket-item .item-details .item-title{color:black;font-weight:400;font-size:28px}.basket-item .item-details .item-pricing{margin:0.5em 0}.basket-item .item-details .quantity{display:flex;margin-bottom:8px}.basket-item .item-details .quantity .quant-btn{cursor:pointer;display:flex;background:#ffaf8a;border:none;align-items:center;justify-content:center}.basket-item .item-details .quantity .quant-btn:hover{-webkit-filter:brightness(0.9);filter:brightness(0.9)}.basket-item .item-details .quantity .quant-btn svg{stroke-width:1px;height:25px}.basket-item .item-details .quantity .quant-amount{display:flex;justify-content:center;align-items:center;background:#ececec;padding:0.5em 1em}.basket-item .item-details .remove{border:1px solid #ffaf8a;background:white;padding:0.1em 0.25em;transition:all 0.15s ease-in-out;cursor:pointer}.basket-item .item-details .remove:hover{background:#ffaf8a}

